'use strict';

var promisify = require('promisify-es6');

var streamToValue = require('../utils/stream-to-value');

var CID = require('cids');

var LRU = require('lru-cache');

var lruOptions = {
  max: 128
};
var cache = new LRU(lruOptions);

module.exports = function (send) {
  return promisify(function (cid, options, callback) {
    if (typeof options === 'function') {
      callback = options;
      options = {};
    }

    if (!options) {
      options = {};
    }

    var cidB58Str;

    try {
      cid = new CID(cid);
      cidB58Str = cid.toBaseEncodedString();
    } catch (err) {
      return callback(err);
    }

    var node = cache.get(cidB58Str);

    if (node) {
      return callback(null, node.data);
    }

    send({
      path: 'object/data',
      args: cidB58Str
    }, function (err, result) {
      if (err) {
        return callback(err);
      }

      if (typeof result.pipe === 'function') {
        streamToValue(result, callback);
      } else {
        callback(null, result);
      }
    });
  });
};