'use strict';

var promisify = require('promisify-es6');

module.exports = function (send) {
  return promisify(function (hash, opts, callback) {
    if (typeof hash === 'function') {
      callback = hash;
      opts = null;
      hash = null;
    }

    if (typeof opts === 'function') {
      callback = opts;
      opts = null;
    }

    if (hash && hash.type) {
      opts = hash;
      hash = null;
    }

    send({
      path: 'pin/ls',
      args: hash,
      qs: opts
    }, function (err, res) {
      if (err) {
        return callback(err);
      }

      callback(null, Object.keys(res.Keys).map(function (hash) {
        return {
          hash: hash,
          type: res.Keys[hash].Type
        };
      }));
    });
  });
};