'use strict';

var promisify = require('promisify-es6');

module.exports = function (send) {
  return promisify(function (callback) {
    send({
      path: 'log/ls'
    }, function (err, result) {
      if (err) {
        return callback(err);
      }

      callback(null, result.Strings);
    });
  });
};