'use strict';

var promisify = require('promisify-es6');

var CID = require('cids');

var _require = require('ipld-dag-pb'),
    DAGNode = _require.DAGNode;

var SendOneFile = require('../utils/send-one-file');

var once = require('once');

module.exports = function (send) {
  var sendOneFile = SendOneFile(send, 'object/put');
  return promisify(function (obj, options, _callback) {
    if (typeof options === 'function') {
      _callback = options;
      options = {};
    }

    var callback = once(_callback);

    if (!options) {
      options = {};
    }

    var tmpObj = {
      Data: null,
      Links: []
    };

    if (Buffer.isBuffer(obj)) {
      if (!options.enc) {
        tmpObj = {
          Data: obj.toString(),
          Links: []
        };
      }
    } else if (DAGNode.isDAGNode(obj)) {
      tmpObj = {
        Data: obj.Data.toString(),
        Links: obj.Links.map(function (l) {
          return {
            Name: l.Name,
            Hash: l.Hash.toString(),
            Size: l.Tsize
          };
        })
      };
    } else if (typeof obj === 'object') {
      tmpObj.Data = obj.Data.toString();
      tmpObj.Links = obj.Links;
    } else {
      return callback(new Error('obj not recognized'));
    }

    var buf;

    if (Buffer.isBuffer(obj) && options.enc) {
      buf = obj;
    } else {
      buf = Buffer.from(JSON.stringify(tmpObj));
    }

    var enc = options.enc || 'json';
    var sendOptions = {
      qs: {
        inputenc: enc
      }
    };
    sendOneFile(buf, sendOptions, function (err, result) {
      if (err) {
        return callback(err); // early
      }

      callback(null, new CID(result.Hash));
    });
  });
};