'use strict';

var promisify = require('promisify-es6');

var CID = require('cids');

module.exports = function (send) {
  return promisify(function (template, callback) {
    if (typeof template === 'function') {
      callback = template;
      template = undefined;
    }

    send({
      path: 'object/new',
      args: template
    }, function (err, result) {
      if (err) {
        return callback(err);
      }

      callback(null, new CID(result.Hash));
    });
  });
};