'use strict';

var promisify = require('promisify-es6');

var transform = function transform(res, callback) {
  callback(null, {
    id: res.Id,
    was: res.Was,
    now: res.Now,
    overwrite: res.Overwrite
  });
};

module.exports = function (send) {
  return promisify(function (oldName, newName, callback) {
    send.andTransform({
      path: 'key/rename',
      args: [oldName, newName]
    }, transform, callback);
  });
};