'use strict';

var promisify = require('promisify-es6');

var transform = function transform(res, callback) {
  callback(null, {
    id: res.Keys[0].Id,
    name: res.Keys[0].Name
  });
};

module.exports = function (send) {
  return promisify(function (args, callback) {
    send.andTransform({
      path: 'key/rm',
      args: args
    }, transform, callback);
  });
};